<template>
  <div class="StudentInfo">
    <el-card>
        <el-main>
            <el-table :data="compData" border :fit="true" style="text-align: center">
              <el-table-column prop="name" label="姓名" align="center"></el-table-column>
              <el-table-column prop="sex_text" label="性别" align="center"></el-table-column>
              <el-table-column prop="age" label="年龄" align="center"></el-table-column>
              <el-table-column prop="number" label="学号" align="center"></el-table-column>
              <el-table-column prop="class" label="班级" align="center"></el-table-column>
              <el-table-column prop="state_text" label="状态" align="center"></el-table-column>
              <el-table-column prop="address" label="地址" align="center"></el-table-column>
              <el-table-column prop="phone" label="联系方式" align="center"></el-table-column>
              <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                  <el-button type="danger" size="mini" icon="el-icon-delete" @click="del(scope.row)"
                  ></el-button>
                </template>
              </el-table-column>
            </el-table>
        </el-main>
        <el-footer style="height:10%;">
          <el-card>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 20, 30,50]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
          </el-card>
        </el-footer>
    </el-card>
  </div>
</template>

<script>
import StudentsData from '@/assets/Json/StudentsData.json'
export default {
  name: "Student",
  components:{
  },
  data() {
    return {
      tableData:StudentsData,
      currentPage:1,  //当前页数
      pageSize:10,    //每一页显示条数
      total:StudentsData.length,        //总条数
    }
  },
  mounted(){
    window.name='Student';
  },
  computed:{
    compData(){
      return  this.tableData.slice((this.currentPage-1)*this.pageSize,this.currentPage*this.pageSize)
    },
  },
  methods: {
    del(row) {
      console.log(row)
      this.$message({message:'没有操作权限',type:'warning'})
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val
      this.currentPage = 1
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val
    },
  },
}
</script>

<style scoped lang="scss">
.StudentInfo {
  width: 100%;
  height: 100%;
  .el-card{
    width: 100%;
    height: 100%;
    ::v-deep.el-card__body{
      width: 100%;
      height: 100%;
      padding: 0;
        .el-main{
          height: 85%;
          width: 100%;
          .el-table{
            width: 100%;
            height: 100%;
            .el-table__header-wrapper{
              height: 15%;
            }
            .el-table__body-wrapper{
              overflow: overlay;
              position: relative;
              height: 85%;
            }
          }
        }
        .el-footer{
          height: 10%;
          width: 100%;
          margin: 5px 0;
          .el-pagination{
            padding: 1%;
          }
        }
    }
  }
}


</style>